            }
        },
        beforeMount() {
            this.flightData = this.$store.getters.getBookingFlightDetail;
            this.searchData = this.$store.getters.getUserSearch;
            if (this.searchData.adults === undefined || this.flightData.summary === undefined) {
                this.$router.push("/");
            }
            let pnrObj = this.$store.getters.getPnrObj;
            this.PnrReq = pnrObj.req;
            this.pnrResp = pnrObj.res;
        },
        methods: {
            getDDMMMYYYY,
            addDaysToDate,
            minusDaysToDate,
            formatDate,
            getTime,
            getDifference,
            getMinutesToString,
