export async function getLocalFlightsData() {
    return await axios.get("/meta-data/flights/bfm-15-06-LHE-30-06-LHR.json").then((response) => {
        // Handle success
        return processSearchFlights(response.data.groupedItineraryResponse);
    }).catch((error) => {
        console.error("An error occurred.");
        console.error(error);
        return [];
    }).finally(() => {
        //Perform action in always
    });
}

