//https://developer.sabre.com/docs/rest_apis/air/search/bargain_finder_max/reference-documentation#/default/createBargainFinderMax

async function saveResponse(params, req, res, status) {
    try{
        let src = (process.env.NODE_ENV === 'production') ? "prod" : "local";
        let mode = (useLiveApi) ? "live" : "test";
        let flightDetails = store.getters.getBookingFlightDetail;
        let userSearch = store.getters.getUserSearch;
        let recordData = {
            "request": JSON.stringify(req),
            "response": JSON.stringify(res),
            "status": status,
            "user_data": JSON.stringify(userSearch),
            "sabre_mode" : mode,
            "source": src,
            "flight_details" : JSON.stringify(flightDetails),
            "passenger_details" : JSON.stringify(params)
        };
        await axios.post(`https://airtravel.b4a.io/classes/pnrs`, recordData, {
            "headers": {
                "Content-Type": "application/json",
                "X-Parse-Application-Id": "zSynevoKRzE145cEwZdsGBdaRPNhPWJqjAPUQ58m",
                "X-Parse-REST-API-Key": "VL120e14kYqK1FdhdcfB0GNuZaobLBpUt135RXop"
            }
        }).then((response) => {
            return true;
        }).catch((error) => {
            return false;
        }).finally(() => {
            //Perform action in always
        });
    }catch (e) {
      return false;
    }
}
