import {BFM_Request} from "./bfm-query";
import {PNR_Request} from "./pnr-query";
import {
    getDDMMMYYYY,
    addDaysToDate,
    minusDaysToDate,
    formatDate,
    getTime,
    getDateFromString,
    getFlightsQueryString,
    getFlightObject,
    isLocalHost,
    getFormattedDob,
    getMmDd,
    getFullDateTime,
    getCabinValue,
} from '../api/utils.js';

export function getBfmRequestObject(reqData) {
    let bfmQueryObj = BFM_Request;

    //Set Cabin Type
    bfmQueryObj.OTA_AirLowFareSearchRQ.TravelPreferences.CabinPref[0].Cabin = getCabinValue(reqData.cabinType);

    //Seats Requested
    let seats = reqData.adults + reqData.childs + reqData.infants;
    bfmQueryObj.OTA_AirLowFareSearchRQ.TravelerInfoSummary.SeatsRequested = [];
    bfmQueryObj.OTA_AirLowFareSearchRQ.TravelerInfoSummary.SeatsRequested.push(seats);
    //Set Direct flights
    if (reqData.directFlights) {
        bfmQueryObj.OTA_AirLowFareSearchRQ.TravelPreferences.MaxStopsQuantity = 0;
        bfmQueryObj.DirectFlightsOnly = true;
    }

    //Set travel places Info
    bfmQueryObj.OTA_AirLowFareSearchRQ.OriginDestinationInformation = [];
    let travelInfoStr = '{"RPH":"1","DepartureDateTime":"date","DestinationLocation":{"LocationCode":"to"},"OriginLocation":{"LocationCode":"from"}}';
    if (reqData.tripType === 2) {
        if (reqData.locs.length > 0) {
            let cities = reqData.locs;
            for (let l = 0; l < cities.length; l++) {
                let city = cities[l];
                let rphNum = l + 1;
                let depDateObj = getDateFromString(city.depDate) + "T00:00:00";
                let trvlObjC = JSON.parse(travelInfoStr);
                trvlObjC.RPH = rphNum.toString();
                trvlObjC.DepartureDateTime = depDateObj;
                trvlObjC.DestinationLocation.LocationCode = city.to;
                trvlObjC.OriginLocation.LocationCode = city.from;
                bfmQueryObj.OTA_AirLowFareSearchRQ.OriginDestinationInformation.push(trvlObjC);
            }
        }
    } else {
        let dpDateObj = getDateFromString(reqData.departureDate) + "T00:00:00";
        let trvlObjA = JSON.parse(travelInfoStr);
        //It's One way trip
        trvlObjA.RPH = "1";
        trvlObjA.DepartureDateTime = dpDateObj;
        trvlObjA.DestinationLocation.LocationCode = reqData.to;
        trvlObjA.OriginLocation.LocationCode = reqData.from;
        bfmQueryObj.OTA_AirLowFareSearchRQ.OriginDestinationInformation.push(trvlObjA);
        if (reqData.tripType === 1) {
            let rtDateObj = getDateFromString(reqData.returnDate) + "T00:00:00";
            let trvlObjB = JSON.parse(travelInfoStr);
            //It's round trip
            trvlObjB.RPH = "2";
            trvlObjB.DepartureDateTime = rtDateObj;
            trvlObjB.DestinationLocation.LocationCode = reqData.from;
            trvlObjB.OriginLocation.LocationCode = reqData.to;
            bfmQueryObj.OTA_AirLowFareSearchRQ.OriginDestinationInformation.push(trvlObjB);
        }
    }
    return bfmQueryObj;
}

export function getPnrRequestObject(params) {
    let contactNumberStr = '{"Phone":"SET","PhoneUseType":"H"}';
    let personNameStr = '{"NameReference":"SET","GivenName":"SET","Surname":"SET"}';
    let flightSegmentStr = '{"DestinationLocation":{"LocationCode":"SET"},"MarketingAirline":{"Code":"SET","FlightNumber":"SET"},"OriginLocation":{"LocationCode":"SET"},"FlightNumber":"SET","NumberInParty":"SET","ResBookDesigCode":"SET","Status":"NN"}';
    let personInfoStr = '{"PersonName":{"GivenName":"SET","Surname":"SET","DateOfBirth":"SET","Gender":"M","NameNumber":"1.1"}}';
    let emailInfoStr = '{"Address":"email"}';

    let pnrQueryObj = PNR_Request;
    //set contact info
    pnrQueryObj.CreatePassengerNameRecordRQ.TravelItineraryAddInfo.CustomerInfo.ContactNumbers.ContactNumber = [];
    //set person email
    pnrQueryObj.CreatePassengerNameRecordRQ.TravelItineraryAddInfo.CustomerInfo.Email = [];
    //set person name
    pnrQueryObj.CreatePassengerNameRecordRQ.TravelItineraryAddInfo.CustomerInfo.PersonName = [];
    //set person information
    pnrQueryObj.CreatePassengerNameRecordRQ.SpecialReqDetails.SpecialService.SpecialServiceInfo.SecureFlight = [];
    let pasCount = 0;
    params.passengers.forEach(function (o) {
        let fullName = `${o.status} ${o.first_name} ${o.last_name}`;
        if (pasCount === 0) {
            //set contact info
            let contactNumberObj = JSON.parse(contactNumberStr);
            //contactNumberObj.NameNumber = o.first_name;
            contactNumberObj.Phone = params.phone;
            pnrQueryObj.CreatePassengerNameRecordRQ.TravelItineraryAddInfo.CustomerInfo.ContactNumbers.ContactNumber.push(contactNumberObj);

            let emailInfoObj = JSON.parse(emailInfoStr);
            emailInfoObj.Address = params.email;
            pnrQueryObj.CreatePassengerNameRecordRQ.TravelItineraryAddInfo.CustomerInfo.Email.push(emailInfoObj);
        }
        //set person name
        let personNameObj = JSON.parse(personNameStr);
        personNameObj.NameReference = o.status;
        personNameObj.GivenName = o.first_name;
        personNameObj.Surname = o.last_name;
        if(o.ageGroup === "infant"){
            personNameObj.Infant = true;
        }
        pnrQueryObj.CreatePassengerNameRecordRQ.TravelItineraryAddInfo.CustomerInfo.PersonName.push(personNameObj);

        //set person info
        let personInfoObj = JSON.parse(personInfoStr);
        personInfoObj.PersonName.GivenName = o.first_name;
        personInfoObj.PersonName.Surname = o.last_name;
        personInfoObj.PersonName.DateOfBirth = getFormattedDob(o.dob);
        personInfoObj.PersonName.Gender = getGender(o.status);
        personInfoObj.PersonName.NameNumber = "1." + (pasCount + 1);

        pnrQueryObj.CreatePassengerNameRecordRQ.SpecialReqDetails.SpecialService.SpecialServiceInfo.SecureFlight.push(personInfoObj);
        pasCount += 1;
    });//end loop

    //set flight segment
    pnrQueryObj.CreatePassengerNameRecordRQ.AirBook.OriginDestinationInformation.FlightSegment = [];
    let depAirport = "";
    let depDateTime = "";
    let depAirlineCode = "";
    let segCount = 0;
    params.segments.forEach(function (seg) {
        let schedules = seg.schedules;
        let schCount = 0;
        schedules.forEach(function (sch) {
            if (segCount === 0 && schCount === 0) {
                depAirport = sch.departureAirport;
                depDateTime = sch.departureDateTime;
                depAirlineCode = sch.airlineCode;
            }
            let fso = JSON.parse(flightSegmentStr);
            fso.ArrivalDateTime = getFullDateTime(sch.arrivalDateTime);
            fso.DestinationLocation.LocationCode = sch.arrivalAirport;
            fso.DepartureDateTime = getFullDateTime(sch.departureDateTime);
            fso.OriginLocation.LocationCode = sch.departureAirport;
            fso.MarketingAirline.Code = sch.airlineCode;
            fso.MarketingAirline.FlightNumber = sch.flightNumber.toString();
            fso.FlightNumber = sch.flightNumber.toString();
            fso.NumberInParty = params.seatsRequired.toString();//seats required
            fso.ResBookDesigCode = getCabinValue(params.cabinType);//Booking Class
            pnrQueryObj.CreatePassengerNameRecordRQ.AirBook.OriginDestinationInformation.FlightSegment.push(fso);
            schCount += 1;
        });
        segCount += 1;
    });

    pnrQueryObj.CreatePassengerNameRecordRQ.MiscSegment.OriginLocation.LocationCode = depAirport;
    pnrQueryObj.CreatePassengerNameRecordRQ.MiscSegment.DepartureDateTime = getMmDd(depDateTime);//should be MM-DD
    pnrQueryObj.CreatePassengerNameRecordRQ.MiscSegment.VendorPrefs.Airline.Code = depAirlineCode;
    pnrQueryObj.CreatePassengerNameRecordRQ.MiscSegment.NumberInParty = params.seatsRequired;
    pnrQueryObj.CreatePassengerNameRecordRQ.MiscSegment.Text = params.specialText;
    pnrQueryObj.CreatePassengerNameRecordRQ.SpecialReqDetails.SpecialService.SpecialServiceInfo.Service[0].Text = params.specialText;

    pnrQueryObj.CreatePassengerNameRecordRQ.AirPrice[0].PriceRequestInformation.OptionalQualifiers.PricingQualifiers.PassengerType[0].Quantity = params.seatsRequired.toString();
    return pnrQueryObj;
}

function getGender(status) {
    let gender = "";
    let stLower = status.toLowerCase();
    switch (stLower) {
        case "inf":
            gender = "U";
            break;
        case "mr":
            gender = "M";
            break;
        case "mrs":
        case "ms":
        case "miss":
        case "mist":
            gender = "F";
            break;
        default:
            gender = "M";
    }
    return gender;
}
